@keyframes right {
  33% {
    opacity: 0.25; }
  66% {
    opacity: 1; }
  100% {
    opacity: 0.25; } }

.Error404 {
  padding-top: 80px;
  width: 100%;
  height: 100vh;
  background-image: url("../images/blank-note-unsplash.jpg");
  background-size: cover;
  background-position: bottom; }

.Error404--h2 {
  color: #fff;
  font-size: 5rem;
  font-weight: 500;
  margin-bottom: 16px; }

.Error404--Link {
  color: #0093c4;
  font-size: 3rem;
  cursor: pointer; }

.Login {
  grid-column: 1/6; }

.Login--button {
  max-width: 220px;
  min-height: 40px;
  padding: 8px 16px;
  text-align: left;
  width: 100%;
  border: none;
  border-radius: 2px;
  height: 36px;
  min-width: 64px;
  outline: none;
  cursor: pointer;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  justify-self: center;
  background-color: #fff; }

.Login--icon {
  height: 18px;
  width: 18px;
  vertical-align: middle; }

.Login--text {
  font-size: 14px;
  font-weight: 500;
  padding-left: 16px;
  color: #757575; }
